import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {BannerBlock} from './BaseBlock';

export const ActionCallout = () => 
<BannerBlock className="Callout" name="Our Appeal" path="callToAction" photos={[{name:"Our Appeal", url:"/assets/CastleThumb.png"}]}>
    <p>All items presented are examples of work you might commission directly.</p>
    <p>Some items are also currently marked for sale.</p>
    <p>Currently behind on rent and looking for help.</p>
    <p>Offering commissions and am available for freelance and contract work.</p>
    <p>Connect with us on Discord to talk about how you can help.</p>
</BannerBlock>

const ActionStyles = styled.section`
margin: auto;
margin-top: 2rem;
background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 100%);
min-height: 100vh;

header{
    max-width: 854px;
    margin: 0 auto;
    padding-top: 1rem;
    iframe{
        max-width: 854px;
        width: 854px; /* For Chrome */
        max-height:480px;    
        margin: auto;
        box-shadow: 4px 4px 5px 0px #2e251e80;
    }  
}

main{
    margin: auto;
    padding: 1rem;
}

footer{
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}

.CallBack{
    flex: 1 0 100%;
    text-align: center;
}

a.External{
    flex: 1 0 40%;
    margin:20px;
    img {
        display: block;
        float: left;
    }
}


`

export const CallToAction = () =>
    <ActionStyles>
        <header className="callToActionHeader">
        <h2>Team Tana is scrambling for rent and needs your help finding work and support quick!</h2>
        <iframe frameBorder="0" scrolling="no" marginHeight="0" marginWidth="0" width="854" height="480" src="https://www.youtube.com/embed/sz9JrVUOV6c"></iframe>
        </header>
        <main>
        <h2>About Aleesa Pixel Tana</h2>
        <p>I‚Äôm a university trained pencil-and-paper animator that loves pixel art and low-poly 3D models.  I also know how to write UI and shader code, and enjoy contributing meaningfully to game design and user experience.</p>
        <p>I enjoy creating character and environmental artwork that evokes a living story and utilizes a variety of cartoon stylization. Some of my greatest inspirations are from manga, comic book and animation artists including Wendy Pini, CLAMP, Bryan Konietzko, Hayao Miyazaki and many more.</p>
        <p>My game development path started with RPG Maker, with the hope of creating a game in the spirit of The Legend of Zelda and Illusion of Gaia.  I‚Äôve worked on several independent games, including To The Moon and its sequels.  I‚Äôve also worked on the Minecraft Redmond Art Team and enjoyed it so much that I‚Äôm applying to be a Marketplace Partner.   I‚Äôm excited about my continued journey learning both Unity and Godot game engines.</p>
        <p>My favorite mediums to work in are colerase pencils, alcohol markers, gouache, pixel art and hand painted 3D. I work hard to emphasize a sense of motion in my art style beyond still flat pictures by introducing it through animation, interactivity, third-dimensionality and the play of light.</p>
        <p>I want to make art that inspires others to be creative. I especially want this message to reach and encourages others that, like me, have suffered trauma; To believe that healing is possible through creativity.</p>
        
        <h2>About Liz Tana</h2>
        <p>Game and Web developer with Server, QA Automation, Animation and Shader experience.</p>
        <p>I program in GDScript in the Godot game engine and Javascript for server and web. C, C++ C#, Java, Python and Ruby are all familiar as well.</p>
        <p>Major gaming inspirations have come from character driven action RPGs, especially the Secret of Mana series, Beyond Oasis and Terranigma with their immersive blend of high quality story and gameplay loops in both the short and long term.</p>
        <p>Also picked up Necromunda and 40k in my early teens and have been an avid fan of Warmachine and Hordes since their respective launches, along side many other games.</p>
        <p>Those influences drive me to bring a greater focus on personal storytelling into these RPGs and into system-driven games as well. I've been playing rogue-likes, Civ-spawn and their ilk since some of the earliest entries, and have always appreciated the personalizing touches inside of a simulated larger world.</p>
        <p>I grew up reading fantasy and sci-fi, with Terry Pratchett and Anne McCaffrey being special favorites. I also grew up on natural history as a past time, and I want to tell stories of ecological possibilities in this world. I want to do that by examination of the many themes of history repeating.</p>
        <p>I have a lot still to share from the effort and time I have put into this commission work as a business and into the game development work as well. I hope I can find the support to confront my social anxiety about going back to professional life after my long absence, as well as now being out as non-binary. I could use all the support I can get on these front.</p>
        <h2>What Support Looks Like</h2>
        <p>Support through sharing with your social network, either sites or in person, commissions and work, and financial contributions are all extremely vital. Thank you for your time and we hope you find our efforts worthwhile.</p>
        </main>
        <footer>
        <Link className="CallBack" to="/commissions"><h1>‚ñ∫Visit at our Commissions Gallery to get a good look at what we do‚óÑ</h1></Link>
        <a className="External" href="https://www.patreon.com/Aleesatana"><img src="/assets/Patreon_Mark_White.png" alt="Patreon" width="80px" height="80px"/>
        <h3>To support our ongoing publishing efforts, come see our Patreon</h3></a>
        <a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
        <h3>Join us on Discord, we appreciate the company and it's a great place to discuss commissions, freelancing, or just about anything!</h3>
        </a>
        </footer>
    </ActionStyles>