import React, {useState, Fragment} from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryImg, GalleryItem, RibbonContainer} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

const InnerGalleryMini = ({srcHint,alt,buyPrice,printUrl,commishPrice,children,hide,toggleHide}) =>
    <GalleryItem hidden={hide} key={srcHint}>
        <h2>{alt}</h2>
        <div className='ImageBox'>
            <img onClick={toggleHide} src={`/assets/${srcHint}Thumb.png`} alt={alt} />
        </div>
        <RibbonContainer buyPrice={buyPrice} printUrl={printUrl} commishPrice={commishPrice} />
        <div className="Description">
            {children}
        </div>
        <MiniModal {...{toggleHide, hide, srcHint, alt}} />
    </GalleryItem>

const hintToVideo = {
    "Trolls": "Vkq40avpAn8",
    "PinkMercs": "jkmqNRGyq2M"
}

const MiniModal = ({toggleHide, hide, srcHint, alt}) => 
<div onClick={toggleHide} 
className={`Modal 
${hide? "Modal-exit Modal-exit-active":"Modal-enter Modal-enter-active" }`}>
    {hide? <Fragment /> : <iframe width="560" height="315" src={`https://www.youtube.com/embed/${hintToVideo[srcHint]}`} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen="true"></iframe>}
</div>

export const GalleryMini = (props) =>{
    let [hide,setHide] = useState(true);
    const handleHideClick = (event) => {
        setHide(!hide);
    }
    let combineProps = {...props,hide,toggleHide:handleHideClick}
    return <InnerGalleryMini {...combineProps}/>

}

const NAME = "Miniatures"
export const MinisCallout = () => 
<BannerBlock className="Callout" path={NAME.toLowerCase()} name={NAME} background="/assets/Background1.jpg" photos={[{name:"Miniatures", url:"/assets/TrollsThumb.png"}]}>
    <p>Painted Miniatures of various brands and styles</p>
    <p>Most for sale and are good examples of how to gauge comission quality concerns</p>
</BannerBlock>

export const MinisGallery = () => 
<GalleryPage title="Miniatures">
    <h2>Prices range per model, please come talk to us on Discord!</h2>
    <GalleryMini srcHint="Trolls" alt="Trolls" buyPrice={"15-50"} commishPrice={"15-50"}>
        <p>Has video preview!</p>
        <p>A collection of my Trollblood Hordes army, painted to high tabletop</p>
        <p>Many more and complete units available as well as solos etc</p>
    </GalleryMini>
    <GalleryImg srcHint="Vera" alt="Vera & Worms" buyPrice={40} commishPrice={50}>
        <p>Vera and the Worms</p>
        <p>The inspiration for a capsule adventure line being working on</p>
        <p>Will post complete adventure seed soon</p>
    </GalleryImg>
    <GalleryMini srcHint="PinkMercs" alt="Pink Mercenaries" buyPrice={"15-90"} commishPrice={"20+"}>
        <p>Has video preview!</p>
        <p>Part of my Breast Cancer Brawl army and I would love to see these be prize support for Breast Cancer Brawl or similar events.</p>
        <p>I will price them much lower if so.</p>
    </GalleryMini>
    <GalleryImg srcHint="AngryElves" alt="Angry Elves" buyPrice={35} commishPrice={40}>
        <p>Video preview coming soon!</p>
    </GalleryImg>
    <GalleryImg srcHint="Archer" alt="Archer" commishPrice={40}>
        <p>Archer I painted up as part of commission recently. Basic tabletop quality, quick and fun.</p>
        <p>Includes diorama with lid</p>
    </GalleryImg>
</GalleryPage>
